<template>
  <div class="container">
    <div class="registration-box">
      <div class="registration-form">
        <h2>注册面板</h2>
        <form @submit.prevent="submitForm">
          <div class="user-box">
            <input type="text" name="name" required v-model="name">
            <label>用户名</label>
          </div>
          <div class="user-box">
            <input type="password" name="password" required v-model="password">
            <label>密码</label>
          </div>
          <div class="user-box select-style">
            <select name="role" required v-model="role">
              <option value="" disabled selected>选择注册身份</option>
              <option value="NORMAL">普通用户</option>
              <option value="MANAGER">管理员</option>
            </select>
          </div>
          <button type="submit" class="register-btn">注册</button>
        </form>
      </div>
    </div>
    <!-- 弹窗 -->
    <div v-if="showSuccessPopup" class="popup-overlay">
      <div class="popup-content">
        <div class="popup-header">
          <span class="popup-title">成功</span>
          <button @click="closePopup" class="popup-close-btn">&times;</button>
        </div>
        <div class="popup-body">
         
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      name: "",
      password: "",
      role: "",
      showSuccessPopup: false
    };
  },
  methods: {
    submitForm() {
      const formData = {
        name: this.name,
        password: this.password,
        role: this.role
      };
      axios.post('http://localhost:8090/enroll', formData, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        if (response.status === 200) {
          this.showSuccessPopup = true;
          this.resetForm();
          setTimeout(() => {
            this.$router.push({ name: 'login' });
          }, 3000);
        } else {
          alert('注册失败');
        }
      })
      .catch(error => {
        console.error('Error during registration:', error);
        if (error.response) {
          alert(`注册失败: ${error.response.data.error}`);
        } else if (error.request) {
          alert('请求未得到响应，请检查后端服务是否运行。');
        } else {
          alert('发生错误，请稍后再试。');
        }
      });
    },
    closePopup() {
      this.showSuccessPopup = false;
    },
    resetForm() {
      this.name = '';
      this.password = '';
      this.role = '';
    }
  }
};
</script>

<style scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #00aaff, #00ff88);
}

.registration-box {
  width: 100%;
  max-width: 400px;
  padding: 40px;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
}

.registration-form h2 {
  margin: 0 0 30px;
  color: #fff;
  text-align: center;
}

.user-box {
  position: relative;
  margin-bottom: 30px;
}

.user-box input,
.select-style select {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}

.user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: 0.5s;
}

.user-box input:focus ~ label,
.user-box input:valid ~ label {
  top: -20px;
  color: #03e9f4;
  font-size: 12px;
}

.register-btn {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: #03e9f4;
  font-size: 16px;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  width: 100%;
  text-align: center;
}

.register-btn:hover {
  background: #03e9f4;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 100px #03e9f4;
}

/* 弹窗样式 */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  max-width: 400px;
  width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.3s ease-in-out;
}

.popup-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.popup-title {
  font-size: 18px;
  color: #4caf50;
  font-weight: 600;
}

.popup-close-btn {
  background: transparent;
  border: none;
  font-size: 24px;
  color: #999;
  cursor: pointer;
  transition: color 0.3s ease;
}

.popup-close-btn:hover {
  color: #333;
}

.popup-body p {
  margin: 0;
  color: #333;
  font-size: 16px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>

